@import url(https://fonts.googleapis.com/css2?family=Cinzel&display=swap);
body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

.App{text-align:center}.App .App-header{width:100%;height:165px;background:rgba(0,16,165,0.637);color:#fff}.App .App-header .App-header-title{padding-top:60px;font-family:"Cinzel",serif}.App h5{float:left;margin-top:15px;padding-top:5px;font-family:"Cinzel",serif}.App .form-select{width:75px;float:left;margin-top:15px;margin-left:10px;font-family:"Cinzel",serif}.App .form-control{margin-top:15px;font-family:"Cinzel",serif}.App .photos{min-height:600px}.App .photos img{transform:scale(0.9);transition:ease 0.2s;cursor:pointer}.App .photos img:hover{transform:scale(1.2)}.App .page-item.active .page-link{z-index:1;color:#fff;background-color:rgba(0,16,165,0.637);border-color:rgba(0,16,165,0.637)}.App .App-footer{width:100%;height:50px;bottom:0;position:fixed;background:rgba(0,16,165,0.637);color:#fff;padding-top:15px;font-family:"Cinzel",serif}

