@import url('https://fonts.googleapis.com/css2?family=Cinzel&display=swap');

$primary-color: rgba(0, 16, 165, 0.637);
$font-color: rgb(255, 255, 255);
$font-app: 'Cinzel', serif;

.App {
  text-align: center;

  .App-header {
    width: 100%;
    height:165px;
    background: $primary-color;
    color: $font-color;

    .App-header-title {
      padding-top: 60px;
      font-family: $font-app;
    }
  }  

  h5 {
    float: left;
    margin-top: 15px;
    padding-top: 5px;
    font-family: $font-app;
  }

  .form-select {
    width: 75px;
    float: left;
    margin-top: 15px;
    margin-left: 10px;
    font-family: $font-app;
  }

  .form-control {
    margin-top: 15px;
    font-family: $font-app;
  }    
  
  .photos {
    min-height: 600px;

    img {
      transform: scale(0.9);
      transition: ease 0.2s;
      cursor: pointer;
    }
  
    img:hover {
      transform: scale(1.2);
    }
  }

  .page-item.active .page-link {
    z-index: 1;
    color: $font-color;
    background-color: $primary-color;
    border-color: $primary-color;
  }

  .App-footer {
    width: 100%;
    height:50px;
    bottom: 0;
    position: fixed;
    background: $primary-color;
    color: $font-color;
    padding-top: 15px;
    font-family: $font-app;
  }
}